@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 1200px;
  }
}
#footer {
  padding: 60px 0;
}
#footer h3 {
  font-size: 20px;
  line-height: 24px;
  color: #006635;
  font-weight: bold;
  margin-bottom: 15px;
}
#footer a {
  font-size: 16px;
  line-height: 20px;
  color: #000;
  width: max-content;
  text-decoration: none;
  transition: all ease 0.3s;
}
#footer a:hover {
  color: #006635;
}
#footer .logo .redes-sociais {
  display: flex;
  gap: 10px;
}
#footer .logo .redes-sociais .linkedin,
#footer .logo .redes-sociais .instagram {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  transition: all ease 0.3s;
}
#footer .logo .redes-sociais .linkedin {
  border: 1px solid #007ab9;
  background-color: #007ab9;
}
#footer .logo .redes-sociais .linkedin:hover {
  background-color: transparent;
}
#footer .logo .redes-sociais .linkedin:hover svg path {
  fill: #007ab9;
}
#footer .logo .redes-sociais .instagram {
  border: 1px solid #265d89;
  background-color: #265d89;
}
#footer .logo .redes-sociais .instagram:hover {
  background-color: transparent;
}
#footer .logo .redes-sociais .instagram:hover svg path {
  fill: #265d89;
}
#footer .logo .redes-sociais a {
  text-decoration: none;
}
#footer .logo .redes-sociais a svg {
  width: 25px;
}
#footer .logo .redes-sociais a svg path {
  transition: all ease 0.3s;
}
#footer .menu {
  gap: 8px;
}
#footer .certificacoes .certificados {
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
}
#footer .links {
  gap: 50px;
}

#copyright {
  background-color: rgba(0, 0, 0, 0.09);
  padding: 14px 0;
  text-align: center;
  font-size: 12px;
  line-height: 16px;
}